/*SelectBox*/
.selected li {
  color: black;
}

/*Badge */
.badgeAboveChildren {
  position: absolute;
  top: 0;
  right: 0;
  border-radius: 50%;
  color: white;
  border: solid 1px white;
  padding: 0.1rem 0.4rem;
  transform: translate(50%, -50%);
  box-shadow:
    rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px,
    rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
}

.badge {
  position: relative;
  top: -1px;
  border-radius: 50%;
  display: inline-block;
  box-shadow:
    rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px,
    rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
}

/*Input */
.inputRoot {
  display: flex;
  flex-direction: row;
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  box-shadow:
    rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
    rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
}
.defaultInput {
  font: inherit;
  letter-spacing: inherit;
  color: black;
  background: white;
  border: 0px;
  outline: none;
  display: block;
  box-sizing: content-box;
  min-width: 0px;
  height: 30px;
  margin: 0px;
}

.innerLabel {
  position: absolute;
  transform: translateY(-50%);
}

.defaultLabel {
  width: 100%;
  color: #999999;
  cursor: default;
  font-size: 1rem;
  padding: 10px;
}

.expandInput {
  height: 40px;
}

/*button*/
:root {
  --button-color: #ffffff;
  --button-bg-color: #212529;
  --button-hover-bg-color: #000000;
  --button-outline-color: #212529;
}

.btn {
  background: var(--button-bg-color);
  color: var(--button-color);

  font-family: 'Noto Sans Kr', sans-serif;
  font-size: 1rem;
  font-weight: 400;
  text-align: center;
  text-decoration: none;

  border: none;
  border-radius: 4px;
  cursor: pointer;

  box-shadow:
    rgba(0, 0, 0, 0.17) 0px -23px 25px 0px inset,
    rgba(0, 0, 0, 0.15) 0px -36px 30px 0px inset,
    rgba(0, 0, 0, 0.1) 0px -79px 40px 0px inset,
    rgba(0, 0, 0, 0.06) 0px 2px 1px,
    rgba(0, 0, 0, 0.09) 0px 4px 2px,
    rgba(0, 0, 0, 0.09) 0px 8px 4px,
    rgba(0, 0, 0, 0.09) 0px 16px 8px,
    rgba(0, 0, 0, 0.09) 0px 32px 16px;

  transition:
    box-shadow 0.3s ease-in-out,
    transform 0.1s ease-in-out;

  margin: 0;
  padding: 0.5rem 1rem;
}

.btn:active {
  box-shadow:
    rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset,
    rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
  transform: translateY(1px);
}

.btn:hover {
  background: var(--button-hover-bg-color);
  box-shadow:
    rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset,
    rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
}

/*https://stackoverflow.com/questions/14750078/style-disabled-button-with-css*/
.btn:disabled,
.btn[disabled] {
  border: 1px solid #999999;
  background-color: #cccccc;
  color: #666666;
  pointer-events: none; /* 클릭 이벤트를 비활성화 */
  cursor: not-allowed; /* 커서를 not-allowed로 변경 */
}

.primary {
  --button-bg-color: #0d6efd;
  --button-hover-bg-color: #3d8bfd;
}
.success {
  --button-bg-color: #28a745;
  --button-hover-bg-color: #53b96a;
}

.danger {
  --button-bg-color: #dc3545;
  --button-hover-bg-color: #e35d6a;
}

.warning {
  --button-bg-color: #ff9900;
  --button-hover-bg-color: #ff8700;
}

.outline {
  outline: 1px solid var(--button-outline-color);
  background: #ffffff;
  color: var(--button-bg-color);
  --button-hover-bg-color: #eeeeee;
  box-shadow: none;
}
.outline:active {
  box-shadow: none;
  background: #ffffff;
}

.outline:hover {
  box-shadow: none;
  background: #ffffff;
}

.outline.primary {
  --button-color: #0d6efd;
  --button-outline-color: #0d6efd;
}

.outline.success {
  --button-color: #28a745;
  --button-outline-color: #28a745;
}
.outline.danger {
  --button-color: #dc3545;
  --button-outline-color: #dc3545;
}

.outline.warning {
  --button-color: #ff9900;
  --button-outline-color: #ff9900;
}

.icon-container {
  display: flex;
  align-items: center;
}
.child-space {
  padding-left: 10px;
}
/*avatar*/
.avatar {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  box-shadow:
    rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px,
    rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
}

/*search*/
.search {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  position: relative;
  outline: none;
  border-radius: 30px;
  padding: 0;
  box-shadow:
    rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
    rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
}

.searchButton {
  border-radius: 50px;
  outline: none;
  border: 0;
  box-shadow:
    rgba(0, 0, 0, 0.17) 0px -23px 25px 0px inset,
    rgba(0, 0, 0, 0.15) 0px -36px 30px 0px inset,
    rgba(0, 0, 0, 0.1) 0px -79px 40px 0px inset,
    rgba(0, 0, 0, 0.06) 0px 2px 1px,
    rgba(0, 0, 0, 0.09) 0px 4px 2px,
    rgba(0, 0, 0, 0.09) 0px 8px 4px,
    rgba(0, 0, 0, 0.09) 0px 16px 8px,
    rgba(0, 0, 0, 0.09) 0px 32px 16px;
}
.searchInputRoot {
  justify-content: center;
  align-items: center;
  box-shadow: none;
}
.searchInput {
  border-radius: 30px;
  outline: none;
  border: 0;
  box-sizing: border-box;
}

@media (max-width: 567px) {
  .search {
    flex-direction: column;
    border-radius: 0;
  }
  .searchInputRoot,
  .searchButton,
  .searchInput {
    position: static;
    width: 100%;
    border-radius: 0;
  }
}

.timerItem {
}

.timerValue {
  color: darkgrey;
}
.timerLabel {
  color: darkgrey;
}

.timerItem:nth-child(odd) {
  background: white;
  flex-grow: 2;
  border-radius: 10px;
  padding: 5px;
  margin-left: 3px;
  margin-right: 3px;
  box-shadow:
    rgb(204, 219, 232) 3px 3px 6px 0px inset,
    rgba(255, 255, 255, 0.5) -3px -3px 6px 1px inset;
}
.timerItem:nth-child(even) {
  flex-grow: 1;
  padding-top: 5px;
}

/*header*/
.header {
  font-family: 'Nunito Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  padding: 15px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header svg {
  display: inline-block;
  vertical-align: top;
}

.header h1 {
  font-weight: 700;
  font-size: 20px;
  line-height: 1;
  margin: 6px 0 6px 10px;
  display: inline-block;
  vertical-align: top;
}

.header button + button {
  margin-left: 10px;
}

.header .username {
  color: #333;
  font-size: 14px;
  margin-right: 10px;
}
